import React, { useState } from "react";
import "./Search.scss";
import SearchIcon from "@mui/icons-material/Search";
import useStore from "../../../store";
import { songsdata } from "../MainContent/SongList/audio";
import { moviesdata } from "../MainContent/MovieList/movies";

const Search = () => {
  const { setSongList, activeKey, movieList, setMovieList } = useStore();
  const [searchTerm, setSearchTerm] = useState("");

  // console.log(activeKey);
  // activeKey1songlistactiveKey2movielist
  const handleInputChange = (e) => {
    const term = e.target.value;
    setSearchTerm(term);

    // 
    if (!term.trim()) {
      if (activeKey === "1") {
        setSongList(songsdata); // 
      } else if (activeKey === "2") {
        setMovieList(moviesdata); // 
      }
    } else {
      //  activeKey 
      if (activeKey === "1") {
        const filteredSongs = songsdata.filter((song) =>
          song.title.toLowerCase().includes(term.toLowerCase())
        );
        setSongList(filteredSongs); // 
      } else if (activeKey === "2") {
        const filteredMovies = movieList.filter(
          (movie) => movie.name.toLowerCase().includes(term.toLowerCase()) //  title 
        );
        setMovieList(filteredMovies); // 
      }
    }
  };

  return (
    <div className="searchWrapper">
      <div className="search-bar">
        <SearchIcon />
        <input
          type="text"
          placeholder="Search"
          value={searchTerm}
          onChange={handleInputChange}
        />
      </div>
    </div>
  );
};

export default Search;
